import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, LoadingController, ToastController} from 'ionic-angular';

import {AuthServiceProvider} from "../../providers/auth-service/auth-service";

import {AddSlaveServerPage} from "../add-slave-server/add-slave-server";

/**
 * Generated class for the SlaveDistributionSelectionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-slave-distribution-selection',
    templateUrl: 'slave-distribution-selection.html',
})
export class SlaveDistributionSelectionPage {

    public userPostData = {
        'token_id': '',
        'user_id': '',
        'hypervisor_id': ''
    };

    public distributions: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public authServiceProvider: AuthServiceProvider, public loadingCtrl: LoadingController, private toastCtrl: ToastController) {
        this.userPostData.token_id = localStorage.getItem('token_id');
        this.userPostData.user_id = localStorage.getItem('user_id');
        this.userPostData.hypervisor_id = this.navParams.get('hypervisor_ID');

        this.loadDistributions();
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad SlaveHypervisorSelectionPage');
    }

    loadDistributions() {
        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        loading.present();

        let logRes = this.authServiceProvider.postData(this.userPostData, 'distributions_list');

        if (logRes['error'] === false) {
            this.distributions = logRes['results']['list'];

            loading.dismiss();
        } else {
            let toast = this.toastCtrl.create({
                message: logRes['message'],
                duration: 3000,
                position: 'bottom'
            });

            loading.dismiss();
            toast.present();
        }
    }

    selectDistribution(distribution_id) {
        this.navCtrl.push(AddSlaveServerPage, {'hypervisor_ID': this.userPostData.hypervisor_id, 'distribution_ID': distribution_id});
    }
}



