import {Component} from '@angular/core';
import {IonicPage, LoadingController, NavController, NavParams, ToastController} from 'ionic-angular';
import {AuthServiceProvider} from "../../providers/auth-service/auth-service";

/**
 * Generated class for the SlavePreseedPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-slave-preseed',
    templateUrl: 'slave-preseed.html',
})
export class SlavePreseedPage {

    public userPreseedsData = {
        'token_id': '',
        'user_id': '',
        'distribution_id': ''
    };

    public preseed_distrib: any;

    public userPostData = {
        'token_id': '',
        'user_id': '',
        'master_id': '',
        'slave_id': ''
    };

    public slave_preseed: any;

    constructor(public navCtrl: NavController, public navParams: NavParams, public authServiceProvider: AuthServiceProvider, public loadingCtrl: LoadingController, private toastCtrl: ToastController) {
        this.userPostData.token_id = localStorage.getItem('token_id');
        this.userPostData.user_id = localStorage.getItem('user_id');
        this.userPostData.master_id = localStorage.getItem('vmID');
        this.userPostData.slave_id = this.navParams.get('slaveID');

        this.userPreseedsData.token_id = this.userPostData.token_id;
        this.userPreseedsData.user_id = this.userPostData.user_id;
        this.userPreseedsData.distribution_id = this.navParams.get('distributionID');

        this.loadPreseedForDistribution();
        this.loadSlavePreseed();
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad SlavePreseedPage');
    }

    loadPreseedForDistribution() {
        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        loading.present();

        let logRes = this.authServiceProvider.postData(this.userPreseedsData, 'preseeds_list');

        if (logRes['error'] === false) {
            this.preseed_distrib = logRes['results']['list'];

            loading.dismiss();

        } else {
            let toast = this.toastCtrl.create({
                message: logRes['message'],
                duration: 3000,
                position: 'bottom'
            });

            loading.dismiss();
            toast.present();
        }
    }

    loadSlavePreseed() {
        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        loading.present();

        let logRes = this.authServiceProvider.postData(this.userPostData, 'slave_preseed_list');

        if (logRes['error'] === false) {
            this.slave_preseed = logRes['results']['list'];

            loading.dismiss();
        } else {
            let toast = this.toastCtrl.create({
                message: logRes['message'],
                duration: 3000,
                position: 'bottom'
            });

            loading.dismiss();
            toast.present();
        }
    }

    addSlavePreseed(preseed_id) {
        let postData = {
            'token_id': this.userPostData.token_id,
            'user_id': this.userPostData.user_id,
            'preseed_id': preseed_id,
            'slave_id': this.userPostData.slave_id,
            'master_id': this.userPostData.master_id,
            'execution_order': 0
        };

        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        loading.present();

        let logRes = this.authServiceProvider.postData(postData, 'slave_preseed_add');

        let toast = this.toastCtrl.create({
            message: logRes['message'],
            duration: 3000,
            position: 'bottom'
        });

        loading.dismiss();
        toast.present();
    }

    removePreseed(preseed_id) {
        let postData = {
            'token_id': localStorage.getItem('token_id'),
            'user_id': localStorage.getItem('user_id'),
            'preseed_id': preseed_id
        };

        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        loading.present();

        let logRes = this.authServiceProvider.postData(postData, 'preseeds_remove');

        let toast = this.toastCtrl.create({
            message: logRes['message'],
            duration: 3000,
            position: 'bottom'
        });

        loading.dismiss();
        toast.present();
    }
}



