import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, LoadingController, ToastController} from 'ionic-angular';

import {AuthServiceProvider} from "../../providers/auth-service/auth-service";

import {ShowMasterDetailPage} from "../show-master-detail/show-master-detail";

/**
 * Generated class for the AddSlaveServerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-add-slave-server',
    templateUrl: 'add-slave-server.html',
})
export class AddSlaveServerPage {

    public userPostData = {
        'token_id': '',
        'user_id': '',
        'master_id': '',
        'hypervisor_id': '',
        'distribution_id': '',
        'name': '',
        'username': '',
        'password': '',
        'memory': '',
        'storage': ''
    };

    constructor(public navCtrl: NavController, public navParams: NavParams, public authServiceProvider: AuthServiceProvider, public loadingCtrl: LoadingController, private toastCtrl: ToastController) {
        this.userPostData.token_id = localStorage.getItem('token_id');
        this.userPostData.user_id = localStorage.getItem('user_id');
        this.userPostData.hypervisor_id = this.navParams.get('hypervisor_ID');
        this.userPostData.distribution_id = this.navParams.get('distribution_ID');
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad AddSlaveServerPage');
    }

    createSlave() {
        let loading = this.loadingCtrl.create({
            content: 'Please wait...'
        });

        let logRes = this.authServiceProvider.postData(this.userPostData, 'slave_server_create');

        if (logRes['error'] === false) {

            localStorage.setItem('vmID', this.userPostData.master_id);
            loading.dismiss();
            this.navCtrl.popTo(ShowMasterDetailPage);

        } else {
            let toast = this.toastCtrl.create({
                message: logRes['message'],
                duration: 3000,
                position: 'bottom'
            });

            loading.dismiss();
            toast.present();
        }
    }
}



